<template lang="pug">
.curso-main-container.pb-3.tema2
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span 2
      h1 Buenas prcticas en la comunicacin del riesgo

    .row.mb-5
      .col-md-4.mb-0
        figure
            img(src='@/assets/curso/temas/tema2/img-1.png', alt='')
      .col-md-8
        p La comunicacin de riesgos utiliza variadas tcnicas de comunicacin, entre las cuales estn los medios de comunicacin social y medios de comunicacin masivos, as como los grupos de inters y motivacin comunitaria. Igualmente, requiere de la comprensin de las percepciones de las partes interesadas, de las sospechas y creencias, as como de sus conocimientos y prcticas.
        .tarjeta.tarjeta.p-4.mb-5.mt-5(style="background-color:#EAF2E1")(data-aos="flip-up")
          .row.justify-content-center.align-items-center 
            .col-lg-3.mb-3
              figure(data-aos="fade-right")
                img(src='@/assets/curso/temas/tema2/decorador-1.png', alt='Texto que describa la imagen')
            .col-lg-9
              p recuerde que la comunicacin de riesgos efectiva debe ser capaz de identificar y poder manejar desde un inicio los rumores, as como la desinformacin y otros desafos de la comunicacin.

    .row.align-items-center.mb-3
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/temas/icono.svg', style="width: 30px" ,alt='').m-auto
      .col-auto.bg-c8(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 Confianza y credibilidad

    p.mb-5 Estos principios son fundamentales en la comunicacin del riesgo. Si no hay confianza, la poblacin <strong>no va a creer</strong> en la informacin que ofrezcan las autoridades, o no acatan las recomendaciones. Existen algunas acciones que permiten mantener la confianza, se pueden identificar a continuacin: 

    ImagenInfograficaB.color-acento-botones.mb-4.desktop
      template(v-slot:imagen)
        figure
          img(src='@/assets/curso/temas/tema2/img-2-1.png', alt='Imagen decorativa')

      div(x="4%" y="13%" numero="+")
        .row
          .col-md-6.mb-4.mb-md-0
            h3 No mentir
            p No hay que dar ms tranquilidad de la necesaria. Para el caso de Covid-19, el gobierno se present en los medios de comunicacin informando que todo ir bien cuando, de hecho, no se sabe si ser as. En otras palabras, hay que evitar decir que todo ir bien, es necesario decir lo que sabe en ese momento y lo que el gobierno est haciendo al respecto. Es importante comunicar lo que realmente se puede y se debe hacer.

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema2/img-3.png', alt='Imagen decorativa')

      div(x="4%" y="32%" numero="+")
        .row
          .col-md-6.mb-4.mb-md-0
            h3 No minimizar la preocupacin
            p No hay que minimizar la preocupacin de la poblacin, por la presencia de cualquier enfermedad. Hay que reconocer el temor de las personas. Las personas pueden estar atemorizadas, lo cual es una reaccin saludable, no se debe insinuar a la poblacin, no se preocupe.

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema2/img-4.png', alt='Imagen decorativa')

      div(x="4%" y="50%" numero="+")
        .row
          .col-md-6.mb-4.mb-md-0
            h3 Reconocer la incertidumbre
            p Se debe tener en cuenta que las personas presentan muchas dudas, predicciones al futuro sin resolver, esto es entendible y lo que debe hacerse es dar respuestas precisas y no dar informacin a medias que permita a las personas generar inferencias sin argumentos. 

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema2/img-5.png', alt='Imagen decorativa')

      div(x="4%" y="69%" numero="+")
        .row
          .col-md-6.mb-4.mb-md-0
            h3 Explicar las acciones 
            p No se debe decir que el gobierno tiene la situacin bajo control; en lugar de eso, se debe explicar a detalle y con la mayor sinceridad todas las acciones que se estn desarrollando para manejar y dar solucin a la situacin. 

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema2/img-6.png', alt='Imagen decorativa')

      div(x="4%" y="86%" numero="+")
        .row
          .col-md-6.mb-4.mb-md-0
            h3 Ser emptico 
            p La empata, lo que en ocasiones se denomina hablar con el corazn (por ejemplo, no diga s cmo se siente, sino: es normal sentir ansiedad y esta es una situacin preocupante para la comunidad y para todos nosotros).

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema2/img-7.png', alt='Imagen decorativa')

      div(x="95%" y="13%" numero="+")
        .row
          .col-md-6.mb-4.mb-md-0
            h3 Ser el primero en dar la informacin 
            p Hay que dar la informacin a tiempo. Se debe informar a la poblacin, por ejemplo, que existe un caso o un presunto caso de Covid-19 tan pronto como tenga conocimiento de ello. No espere a hacerlo pblico cuando ya se haya convertido en un rumor. El anuncio temprano, es muestra de liderazgo, ello genera confianza. Los anuncios tardos dan lugar a especulaciones, lo que permite que las personas se pregunten hasta qu punto es realmente mala la situacin

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema2/img-8.png', alt='Imagen decorativa')

      div(x="95%" y="32%" numero="+")
        .row
          .col-md-6.mb-4.mb-md-0
            h3 Reconocer las equivocaciones 
            p Si existe un error, corrjalo rpidamente. Eso muestra su lado humano y genera confianza porque no ha tratado de ocultar informacin.

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema2/img-9.png', alt='Imagen decorativa')

      div(x="95%" y="50%" numero="+")
        .row
          .col-md-6.mb-4.mb-md-0
            h3 Evitar la estigmatizacin 
            p Hay que evitar la estigmatizacin. Es as, que para el ejemplo de Covid-19, muchas personas naturales de China o con rasgos/orgenes asiticos estn sufriendo discriminacin de forma injustificada.

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema2/img-10.png', alt='Imagen decorativa')
      
      div(x="95%" y="68%" numero="+")
        .row
          .col-md-6.mb-4.mb-md-0
            h3 Ser transparente 
            p Esto significa dar informacin clara, comprensible, sin adornos innecesarios que puedan generar duda. Ser transparente es hablar claro con nuestros interlocutores, expresar la verdad, siendo objetivos.

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema2/img-11.png', alt='Imagen decorativa')

      div(x="95%" y="86%" numero="+")
        .row
          .col-md-6.mb-4.mb-md-0
            h3 Involucrar la comunidad 
            p Es importante tener claro que la comunidad es la afectada con ciertos problemas de salud, es importante que siempre est involucrada en los procesos y las decisiones que se lleven a cabo, no pueden generarse imposiciones, se debe consultar y trabajar en favor de su bienestar. 

          .col-md-6
            figure
              img(src='@/assets/curso/temas/tema2/img-12.png', alt='Imagen decorativa')

    .row.mt-5
      .col-10.col-md-10.col-lg-12.movil(data-aos="fade-right")
