          h3.mb-0.ms-3 Difusin y comunicacin

    p.mb-5 El coordinar la elaboracin de los mensajes y realizar la difusin de forma oportuna y transparente de informacin cientfica entre las organizaciones que tengan relacin con el tema ayudan a evitar confusiones que puedan socavar la confianza de las personas, aumentar el temor y la ansiedad, e impedir las medidas de respuesta.

    .tarjeta.tarjeta.p-4.mb-5.mt-5(style="background-color:#E6EBFB")(data-aos="flip-up")
      .row.justify-content-center.align-items-center 
        .col-lg-4.mb-3
          figure(data-aos="fade-right")
            img(src='@/assets/curso/temas/tema2/decorador-2.svg', alt='Texto que describa la imagen')
        .col-lg-8
          p Ahora bien, para lograr contribuir de manera significativa con la gestin del conocimiento necesario para reducir las inequidades en salud, as como lograr tener un impacto importante en los resultados de salud dentro de la poblacin y a travs de grupos diferenciales de inters por parte del Plan Decenal de Salud Pblica (PDSP), se requiere que muchos elementos de la agenda pblica estn coordinados y no se limiten simplemente a las competencias para legislar, en cuestiones directamente relacionadas con garantizar, a travs de una variedad de estrategias, polticas, programas e iniciativas, el mejoramiento de los servicios de salud; as como garantizar el aumento de la cobertura y disponibilidad de la tecnologa en salud. La difusin y diseminacin del PDSP debe darse a partir de evidencia y datos, y se debe apoyar y concentrar en las siguientes iniciativas y herramientas:

    .row.justify-content-center.align-items-center.mb-3
      .col-8(data-aos="fade-down-right")
        ImagenInfografica.color-acento-botones.mb-5.desktop
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/temas/tema2/img-13-1.svg', alt='Texto que describa la imagen')

          .tarjeta.color-acento-botones.p-3(x="30%" y="8.6%" numero="1")(style="background-color:#F1ECE6")
            p(style="color:#000 !important") Encuentros especializados en que se desarrollan estudios precisos sobre las diversas enfermedades, tratamientos y riesgos, el desarrollo de conocimiento y la toma de decisiones se va dando en la medida en la que se realiza un proceso de dilogo e interactividad entre los especialistas y los participantes.

          .tarjeta.color-acento-botones.p-3(x="74.5%" y="6%" numero="2")(style="background-color:#F1ECE6")
            p(style="color:#000 !important") Es importante promover y desarrollar cursos dirigidos para que los usuarios puedan empoderarse respecto al anlisis social y econmico en relacin con el desarrollo de diversas enfermedades, se busca con la implementacin de dichos cursos y un enfoque de equidad en salud.

          .tarjeta.color-acento-botones.p-3(x="43%" y="22.5%" numero="3")(style="background-color:#F1ECE6")
            p(style="color:#000 !important") Publicaciones de PolicyBriefs como mecanismo para difundir el aporte del uso de la informacin y la gestin del conocimiento en la orientacin de las intervenciones efectivas en diversas regiones y territorios, en el marco del PDSP.
          
          .tarjeta.color-acento-botones.p-3(x="11.8%" y="51%" numero="4")(style="background-color:#F1ECE6")
            p(style="color:#000 !important") Noticias publicadas relacionadas con logros y progresos de las intervenciones y programas definidos en el marco de las dimensiones del PDSP.
          
          .tarjeta.color-acento-botones.p-3(x="77%" y="46.5%" numero="5")(style="background-color:#F1ECE6")
            p(style="color:#000 !important") Permiten acceder a resultados de indicadores para cada dimensin de forma peridica, para fomentar el acceso transparente y el libre uso del PDSP en el pas. Estos servicios y herramientas adicionales, que aparentemente podran limitarse a ser comunes estrategias de difusin y comunicacin, han logrado demostrar ser exitosos en la implementacin de polticas en salud con enfoque de equidad cada regin.
          
          .tarjeta.color-acento-botones.p-3(x="55.3%" y="80%" numero="6")(style="background-color:#F1ECE6")
            p(style="color:#000 !important") La difusin apoyada en diferentes herramientas ha tenido alto impacto y han contribuido con el desarrollo de mtodos y enfoques para mejorar la responsabilidad por la equidad en salud, incentivar los dilogos, foros y debates para promover el pensamiento crtico y el compromiso de aumentar la efectividad de los programas y planes que se enmarquen en el PDSP en el pas.

        .row.justify-content-center.align-items-center.mt-5.mb-3
