    #t_2_1.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.1. Sistemas de informacin de salud en riesgo ambiental, del consumo y sanitarios

    p.mb-5 En Colombia, desde hace ms de 20 aos se encuentran construyendo dos sistemas de gestin y regulacin, los cuales corresponden al conjunto de orientaciones, normas, actividades, recursos, programas e instituciones que permiten la puesta en marcha de los principios y las polticas, para salud, como para el ambiente, y que son regidos por dos Ministerios, as:

    .tarjeta.tarjeta.p-4.mb-5.mt-5(style="background-color:#EAF2E1")(data-aos="flip-up")
      .row.justify-content-center.align-items-center 
        .col-lg-2.mb-3
