              h3.mb-1 Sistemas de Informacin utilizados por el sector salud y por el sector ambiente en Colombia. 
              p.text-small Para conocer algunos sistemas de Informacin utilizados por el sector salud y por el sector ambiente en Colombia, se debe revisar el siguiente documento:

            .col-sm-auto
              a.boton.color-acento-botones.texto-negro(href="https://www.minsalud.gov.co/sites/rid/1/SISTEMA%20DE%20INFORMACI%C3%93N%20PARA%20LA%20VSP.pdf" target="_blank")
                span Descargar
