    #t_2_2.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.2. Conceptos tcnicos elaborados entre especialistas en comunicacin y los tcnicos

    p.mb-5 Por el desarrollo de las pandemias, el trabajo en equipo entre los especialistas en comunicacin del riesgo y los tcnicos se ha convertido en una gran necesidad, y tiene el fin de promover la meta de salud pblica en cuanto a controlar rpidamente los brotes con el menor trastorno posible a la sociedad.

