            .col-sm-auto
              a.boton.color-acento-botones.texto-negro(href="https://idus.us.es/bitstream/handle/11441/67303/revista-comunicacion-ambitos-07-08_47-80.pdf?sequence=1" target="_blank")
                span Descargar
                i.fas.fa-link

    p.mb-5 Como tambin, se presentan las siguientes <strong>estrategias de comunicacin</strong> participativa para involucrar a miembros de la comunidad en el proceso de rastreo de contactos, pero Por qu es fundamental que las comunidades participen en el rastreo de contactos?

    .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5.mb-5
