    p.mb-5 La OCDE identific algunos principios para promover una comunicacin pblica eficaz, estos son:  

    .row.justify-content-center.align-items-center.mb-5
      .col-10.col-md-10.col-lg-12.desktop(data-aos="fade-right")
        figure
          img.mb-2(src='@/assets/curso/temas/tema2/img-15-1.svg', alt='La infografa resalta los principios esenciales para afrontar de manera efectiva los desafos comunitarios a travs de la comunicacin. Se destaca la importancia de comprender las circunstancias locales y fomentar la confianza mediante la colaboracin activa, as como el mantenimiento de una fuerza laboral comprometida y una comunicacin bidireccional inclusiva. Tambin se enfatiza la necesidad de escuchar y responder a los comentarios de manera oportuna, as como evitar la estigmatizacin y los rumores. Se sugiere el uso de tecnologa para el rastreo de contactos y se enfatiza la importancia de coordinarse con todos los actores involucrados, utilizando un lenguaje que fomenta la empata con la audiencia.')

