    #t_2_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.3. Modelo de vigilancia y control sanitario para los productos de uso y consumo humano

    p.mb-5 Es necesario tener claridad en algunos conceptos y aspectos, los cuales hacen parte del referido modelo de vigilancia y control sanitarios, entre los que estn: funciones esenciales de salud pblica, objetos de inspeccin, fiscalizacin sanitaria, contenido general de las actas, concepto sobre los resultados obtenidos en las visitas.

    .row.align-items-center.mb-2
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/temas/icono.svg', style="width: 30px" ,alt='').m-auto
      .col-auto.bg-c8(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 Las Funciones Esenciales de Salud Pblica (FESP)

    .tarjeta.tarjeta.p-4.mb-5.mt-5(style="background-color:#E6EBFB")(data-aos="flip-up")
      .row.justify-content-center.align-items-center 
        .col-lg-2.mb-3
