            img(src='@/assets/curso/temas/tema2/decorador-3.png', alt='Texto que describa la imagen')
        .col-lg-10  
          p Corresponden al conjunto imprescindible de medidas, bajo la responsabilidad exclusiva del estado, que resulta fundamentalmente para alcanzar la meta de la salud pblica: de mejorar, promover, proteger y recuperar la salud de la poblacin mediante una accin colectiva. (OPS/OMS La Salud Pblica en las Amricas).

    p.mb-5 Las autoridades sanitarias del pas, cuando realizan visita de inspeccin sanitaria a los establecimientos objeto de Inspeccin, Vigilancia y Control (IVC), en cumplimiento con la competencia dadas por la Ley 715 de 2001, estn tambin cumpliendo con las Funciones Esenciales de Salud Pblica (FES 2 y FES 6), como se presenta a continuacin:

    .row.justify-content-center.align-items-center.mb-5
      .col-10.col-md-10.col-lg-12.desktop(data-aos="fade-right")
        figure
          img.mb-2(src='@/assets/curso/temas/tema2/img-16-1.svg', alt='La infografa destaca las funciones esenciales de salud pblica, en particular, la importancia de la Vigilancia en Salud Pblica como un proceso continuo de recoleccin, anlisis e interpretacin de datos relacionados con la salud. Se destaca las capacidades institucionales slidas necesarias para abordar brotes de enfermedades transmisibles y no transmisibles, as como para atender aspectos de salud mental y lesiones. Incluye la vigilancia epidemiolgica, el control de la produccin y el consumo de bienes, as como la vigilancia ambiental de la salud. Adems, se subraya la importancia de la vigilancia y el control sanitario para proteger la salud individual y colectiva.')

    .row.justify-content-center.align-items-center.mt-5.mb-5
      .col-10.col-md-10.col-lg-12.movil(data-aos="fade-right")
        figure
          img(src='@/assets/curso/temas/tema2/img-16-2.svg', alt='La infografa destaca las funciones esenciales de salud pblica, en particular, la importancia de la Vigilancia en Salud Pblica como un proceso continuo de recoleccin, anlisis e interpretacin de datos relacionados con la salud. Se destaca las capacidades institucionales slidas necesarias para abordar brotes de enfermedades transmisibles y no transmisibles, as como para atender aspectos de salud mental y lesiones. Incluye la vigilancia epidemiolgica, el control de la produccin y el consumo de bienes, as como la vigilancia ambiental de la salud. Adems, se subraya la importancia de la vigilancia y el control sanitario para proteger la salud individual y colectiva.')

    .row.align-items-center.mb-2
