    p.mb-5 Como son originados de cadenas productivas, el gobierno nacional a travs del Ministerio de Salud y Proteccin Social ha realizado la siguiente propuesta de categoras y nmero de subcategoras a los objetos de IVC, la cual se encuentra validada por las Entidades Territoriales de Salud, dicha propuesta se puede visualizar en la siguiente tabla: 

