    p.mb-5 Tambin se proponen los siguientes <strong>establecimientos objeto de IVC:</strong>

    .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/temas/tema2/img-14.png')})`}"
      )
      .bloque-texto-g__texto.p-4
        ul.lista-ul--color
          li 
            i.fas.fa-circle
            | Establecimientos donde se realice un proceso producto que incluya la elaboracin de bienes o servicios que impliquen transformacin y elaboracin de materias primas donde se utilicen sustancias qumicas.
          br 
          li 
            i.fas.fa-circle
            | Establecimientos que cuenten con marco normativo sanitario especfico.
          br
          li 
            i.fas.fa-circle
            | Establecimientos donde exista alta concentracin de poblacin trabajadora, flotante o albergada de manera temporal o permanente diferente a las unidades habitacionales.
          br
          li 
            i.fas.fa-circle
            | Establecimientos que son objeto de seguimiento por parte de los organismos de control.
          br
          li 
            i.fas.fa-circle
            | Establecimientos que de acuerdo a su actividad comercial de bienes y servicios reporta eventos de inters en salud pblica.
          br
          li 
            i.fas.fa-circle
            | Establecimientos cuya actividad econmica de bienes y servicios exista una alta presencia de factores de riesgo fsico, qumico y biolgico de acuerdo a la evaluacin realizada por las autoridades sanitarias producto de los diagnsticos de IVC.
  
    p.mb-5 As como los subprocesos de inspeccin, vigilancia y control: 

