    p.mb-5 Sobre la <strong>Fiscalizacin Sanitaria,</strong> que es uno de los macro procesos misionales centrales, comprendido por los subprocesos: inspeccin, vigilancia y control de riesgos sanitarios.  Sus objetivos estn en verificar el cumplimiento de las normas sanitarias, as como en identificar los factores de riesgos y potenciales efectos de los procesos productivos sobre la salud humana. 

    .row.align-items-center.mb-2
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/temas/icono.svg', style="width: 30px" ,alt='').m-auto
      .col-auto.bg-c8(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 Contenido general del acta

    .tarjeta.tarjeta.p-4.mb-5.mt-5(style="background-color:#E6EBFB")(data-aos="flip-up")
      .row.justify-content-center.align-items-center 
        .col-lg-2.mb-3
          figure(data-aos="fade-right")
            img(src='@/assets/curso/temas/tema2/decorador-7.png', alt='Texto que describa la imagen')
        .col-lg-10  
          p Las actas generalmente cuentan con bloques de informacin relacionados con las condiciones sanitarias y ambientales, para ser diligenciados y evaluados segn la informacin obtenida del establecimiento. Las condiciones sanitarias y ambientales evaluadas varan segn la complejidad y el marco normativo especifico.
    
    .row.align-items-center.mb-2
      .col-auto.pe-0(style="z-index:2")
        figure
          img(src='@/assets/curso/temas/icono.svg', style="width: 30px" ,alt='').m-auto
      .col-auto.bg-c8(style="z-index:1")
        .p-2
          h3.mb-0.ms-3 Concepto sobre los resultados obtenidos en las visitas

    p.mb-5 Para empezar, se debe aclarar que los conceptos sanitarios son el resultado obtenido de la evaluacin de las condiciones sanitarias y ambientales con base en las visitas de inspeccin sanitaria y el anlisis de los criterios y normas, existen diferentes tipos de conceptos que se obtienen durante este proceso, al revisar el siguiente material se puede conocerlos:

    TabsA.color-acento-botones.mb-5
      .tarjeta(style="background-color:#EAF2E1").p-4(titulo="Concepto favorable")
        p Se emite cuando el objeto de Inspeccin, Vigilancia y Control (IVC) sanitario, obtiene porcentaje de cumplimiento entre el 95 y 100 % y su funcionamiento u operacin no genera riesgos sanitarios a la salud pblica. Finalizada la visita se insta al representante a continuar con el mantenimiento de las condiciones sanitarias y a cumplir con la normatividad vigente a travs de procesos de autorregulacin, autogestin y buenas prcticas sanitarias.

      .tarjeta(style="background-color:#EAF2E1").p-4(titulo="Concepto favorable con requerimiento")
        p Se emite cuando, el objeto de IVC no cumple totalmente con los requisitos y condiciones sanitarias, pero no representa o constituye un riesgo inminente para la salud pblica o la seguridad sanitaria. La calificacin del acta se encuentra entre el 50 % al 94 % del cumplimiento. Finalmente, la autoridad sanitaria realizar los requerimientos y exigencias a que haya lugar.

      .tarjeta(style="background-color:#EAF2E1; max-width:100%").p-4(titulo="Concepto desfavorable")
        p Se emite cuando en el objeto de IVC: 
        ul.lista-ul
          li 
            i.lista-ul__vineta
            | No cumple con los requisitos y condiciones sanitarias establecidas en el marco normativo sanitario obteniendo una calificacin menor o igual a 49 % y constituye un riesgo inminente para la salud pblica.
          li 
            i.lista-ul__vineta
            | En caso de que uno o ms de los aspectos a evaluar, tanto los aspectos preestablecidos en las actas o como los identificados por el inspector, sea calificado como crtico, independiente del porcentaje de cumplimiento obtenido, el concepto sanitario a emitir ser de desfavorable.
          li 
            i.lista-ul__vineta
            | Exista un incumplimiento reiterado en la tercera visita de seguimiento por la emisin de un concepto sanitario favorable con requerimientos. 

    p.mb-5 Para que la autoridad sanitaria pueda emitir el respectivo concepto, todas las actividades deben quedar registradas en formatos estandarizados que faciliten la trazabilidad, seguimiento y anlisis de riesgo relacionado con el objeto de IVC (Resolucin 1229 de 2013, artculo 28).

    Separador 

    #t_2_4.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.4. Programacin de muestreo y visitas a los establecimientos u organismos

    p.mb-5 Existen algunas fases importantes que siguen las autoridades sanitarias para elaborar la programacin de muestreo y visitas. Observe a continuacin las fases necesarias dentro del proceso de muestreo de alimentos y bebidas: 

    .row.justify-content-center.align-items-center.mt-5
      SlyderF.mb-5(columnas="col-lg-6 col-xl-4")
        .tarjeta.color-primario.p-4.col-ipad2
          .row.justify-content-center.mb-3
            .col-8
              img(src='@/assets/curso/temas/tema2/img-17.png' alt='AvatarTop')
          p <strong>Sabe cmo se realiza el proceso de muestreo de alimentos y bebidas?</strong>
          p Lo primero que se debe hacer es determinar los insumos para la toma de muestra y hacer el alistamiento.

        .tarjeta.color-primario.p-4.col-ipad2
          .row.justify-content-center.mb-3
            .col-8
              img(src='@/assets/curso/temas/tema2/img-18.png' alt='AvatarTop')
          p Se coordina con el laboratorio la capacidad operativa teniendo en cuenta la normatividad sanitaria vigente y los lineamientos del INVIMA. Se elabora y firma un acta con los acuerdos que debe entregarse al coordinador de Salud Ambiental.

        .tarjeta.color-primario.p-4.col-ipad2
          .row.justify-content-center.mb-3
            .col-8
              img(src='@/assets/curso/temas/tema2/img-19.png' alt='AvatarTop')
          p Se elabora el programa de muestreo priorizando el enfoque de riesgos y los lineamientos del INVIMA. Se define el tipo de alimentos a muestrear, los municipios y los establecimientos donde se realizar el muestreo.
        
        .tarjeta.color-primario.p-4.col-ipad2
          .row.justify-content-center.mb-3
            .col-8
              img(src='@/assets/curso/temas/tema2/img-20.png' alt='AvatarTop')
          p Se programa la toma de muestra estableciendo ubicacin, fecha y se asigna el personal que realizar la toma de muestra. Dicho profesional deber trasladarse hasta el establecimiento e informar al propietario sobre el muestreo.

        .tarjeta.color-primario.p-4.col-ipad2
          .row.justify-content-center.mb-3
            .col-8
              img(src='@/assets/curso/temas/tema2/img-21.png' alt='AvatarTop')
          p Proceda a tomar la muestra de acuerdo a lo establecido en el Manual de Toma de muestras de alimentos y bebidas para entidades territoriales de salud emitido por el INVIMA. 

        .tarjeta.color-primario.p-4.col-ipad2
          .row.justify-content-center.mb-3
            .col-8
              img(src='@/assets/curso/temas/tema2/img-22.png' alt='AvatarTop')
          p Diligencie el acta y verifique la disponibilidad de insumos entre los que se encuentran: gel antibacterial, nevera color rojo, pilas refrigerantes, guantes desechables, bata blanca, gorro, tapabocas, marcador esptula, termmetro de punzn.
        
        .tarjeta.color-primario.p-4.col-ipad2
          .row.justify-content-center.mb-3
            .col-8
              img(src='@/assets/curso/temas/tema2/img-23.png' alt='AvatarTop')
          p Es fundamental que los entes encargados hagan seguimiento mensual del avance del plan para hacer los correctivos necesarios. Finalmente, es importante realizar entrenamiento constante al personal para evitar errores durante el proceso. 
  
    p.mb-5 Por otro lado, seguidamente podr conocer las etapas necesarias para realizar el muestreo del agua para el consumo humano:  

    AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
      .row(titulo="Paso 1")
        .col-md-6.mb-4.mb-md-0
          p Saba que el agua que consume diariamente ha pasado por un proceso de inspeccin y verificacin?
          p Lo primero que se debe hacer es determinar el nmero de muestras a recoger: con base en lo dispuesto en la Resolucin 2115 de 2007. Posteriormente, se identifica la capacidad operativa del laboratorio teniendo en cuenta la normatividad y los lineamientos establecidos.

        .col-md-6
          figure
            img(src='@/assets/curso/temas/tema2/img-24.png', alt='Imagen decorativa')

      .row(titulo="Paso 2")
        .col-md-6.mb-4.mb-md-0
          p Es importante recordar la elaboracin del acta de la reunin, y firmarla por el personal implicado. Se programan los puntos de muestreo a monitorear durante la vigencia. Se elabora la programacin mensual para la toma de muestras en los puntos, esta programacin es notificada al Laboratorio de Salud Pblica y a las personas prestadoras del servicio de acueducto de cada municipio.
        
        .col-md-6
          figure
            img(src='@/assets/curso/temas/tema2/img-25.png', alt='Imagen decorativa')

      .row(titulo="Paso 3")
        .col-md-6.mb-4.mb-md-0
          p El tcnico debe dirigirse al laboratorio de Salud Pblica, y solicitar frascos de acuerdo al nmero de muestras programadas. Algunos Insumos requeridos son: mechero con alcohol al (96 %), atomizador con alcohol antisptico al (70 %), algodn estril, encendedor, toallas desechables, gel antibacterial, bolsas, recipientes de vidrio y de plstico, medidor de cloro y PH, nevera azul, bata blanca, gorro y guantes.

        .col-md-6
          figure
            img(src='@/assets/curso/temas/tema2/img-26.png', alt='Imagen decorativa')

      .row(titulo="Paso 4")
        .col-md-6.mb-4.mb-md-0
          p La persona designada debe trasladarse hasta el municipio, dirigirse al acueducto y solicitar acompaamiento, luego dirigirse a los puntos de muestreo y realizar la toma segn el manual establecido. Durante la toma se diligencia el acta la cual debe ser firmada por las partes. Finalmente, la persona encargada debe hacer seguimiento mensual del proceso y realizar las correcciones necesarias.

        .col-md-6
          figure
            img(src='@/assets/curso/temas/tema2/img-27.png', alt='Imagen decorativa')
     
    Separador 

